.disabled-tag {
  color: #ccc;
  cursor: not-allowed;
}
.policy-input-item {
  margin-bottom: 25px;
}
.policy-input-item .policy-name-input-wrap {
  margin-bottom: 15px;
}
.policy-input-item .policy-name-input-wrap .policy-name-input-item {
  padding-left: 29px;
  position: relative;
  height: 24px;
}
.policy-input-item .policy-name-input-wrap .policy-name-input-placeholder {
  position: absolute;
  padding: 0 3px;
}
.policy-input-item .policy-name-input-wrap .policy-name-pre {
  visibility: hidden;
  font-size: 16px;
}
.policy-input-item .policy-name-input-wrap .del-policy-btn {
  display: none;
  float: left;
  padding-top: 6px;
  z-index: 1;
  position: relative;
}
.policy-input-item .policy-name-input-wrap:hover {
  margin-left: 0;
}
.policy-input-item .policy-name-input-wrap:hover .del-policy-btn {
  display: inline-block;
}
.policy-input-item .policy-name-input-wrap .policy-name-input {
  font-size: 16px;
  border: none;
  border-bottom: 2px solid #333333;
  outline: none;
  background-color: transparent;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.policy-input-item .fl-icon-revert-hover {
  display: none;
}
.policy-input-item.disabled-policy .policy-name-input {
  border-bottom-color: #CCCCCC;
  color: #ccc;
  cursor: not-allowed;
}
.policy-input-item.disabled-policy .fl-icon-revert-hover {
  display: inline-block;
}
.policy-input-item.disabled-policy .fl-icon-close-hover {
  display: none;
}
.policy-content-input-wrap {
  margin-left: 29px;
  position: relative;
}
.policy-content-input-wrap .policy-content-input-padding {
  width: 95%;
  color: #666;
  line-height: 1.5;
  font-size: 14px;
  position: relative;
  min-height: 60px;
}
.policy-content-input-wrap .policy-content-input-padding .input-placeholder {
  display: block;
  visibility: hidden;
}
.policy-content-input-wrap .policy-content-input-padding .policy-text {
  color: #ccc;
  cursor: not-allowed;
  white-space: pre-wrap;
}
.policy-content-input-wrap .policy-content-input-padding .policy-content-input {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  color: #666;
  outline: none;
  line-height: 1.5;
  font-size: 14px;
  resize: none;
  border: none;
  box-sizing: border-box;
  overflow: hidden;
  background-color: transparent;
}
.policy-content-input-wrap.policy-editing .policy-toolbar {
  display: block;
  animation: showIn 0.3s;
}
.policy-content-input-wrap .policy-toolbar {
  position: absolute;
  left: -29px;
  top: -10px;
  display: none;
}
.policy-content-input-wrap .policy-toolbar .op-btn {
  color: black;
}
.policy-content-input-wrap .policy-toolbar li {
  height: 30px;
  line-height: 30px;
}
@keyframes showIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
.add-policy-btn {
  color: #CCCCCC;
  font-size: 16px;
}
.add-policy-btn .el-icon-plus {
  margin-right: 9px;
}
