.res-intro-wrapper .user-avatar {
  float: right;
  width: 40px;
  height: 40px;
}
.res-intro-wrapper .user-avatar img {
  width: 100%;
  height: 100%;
  border-radius: 50%;
}
.res-intro-wrapper .res-content {
  padding-bottom: 15px;
  margin-bottom: 15px;
  border-bottom: 1px solid #eee;
  padding-right: 55px;
  word-break: break-all;
}
.res-intro-wrapper .res-content .res-name {
  font-size: 19px;
  color: #333;
  margin-bottom: 12px;
  font-weight: bold;
  line-height: 25px;
}
.res-intro-wrapper .res-content .res-origin {
  font-size: 12px;
  color: #999999;
  margin-bottom: 6px;
}
.res-intro-wrapper .res-content .res-author {
  margin-right: 12px;
}
.res-intro-wrapper .res-content .more-detail-link {
  color: #89D1AC;
}
