.resource-item-info {
  margin-top: 15px;
  cursor: pointer;
  padding: 5px;
}
.resource-item-info .res-type {
  display: none;
}
.resource-item-info.resource-item-theme-type-search .res-intro-ft .res-type {
  display: inline-block;
  background-color: #EEEEEE;
  border-radius: 4px;
  padding: 4px 10px;
}
.resource-item-info.resource-item-theme-type-list .res-intro-bd .res-type {
  display: inline-block;
}
.resource-item-info .res-title {
  font-size: 18px;
  color: #333333;
  margin-bottom: 8px;
}
.resource-item-info .res-intro-bd {
  margin-bottom: 8px;
  font-size: 13px;
}
.resource-item-info .res-type {
  margin-right: 15px;
  color: #1696C0;
}
.resource-item-info .res-desc {
  color: #666666;
  font-size: 13px;
}
.resource-item-info .res-intro-ft {
  color: #999;
  font-size: 12px;
}
.resource-item-info .res-author {
  margin-right: 12px;
}
.resource-item-info:hover {
  background-color: #F5F5F5;
  border-radius: 2px;
}
